
import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class LandlingPage extends React.Component {
    render() {
        return <div style={{ width: '100%', margin: 'auto' }}>
            <Grid className="landing-grid">
                <Cell col={12}></Cell>
                <img
                    src="https://cdn.pixabay.com/photo/2016/08/20/05/38/avatar-1606916_960_720.png"
                    alt="avatar"
                    className="avatar-img"
                ></img>

                <div className="banner-text">
                    <h1>Full Stack Web Developer</h1>
                    <hr />
                    <p>HTML/CSS | BootStrap | JavaScript | NodeJs | Express | MongoDB | AWS</p>
                    <div className="social-links">
                        {/* Linkedin */}
                        <a href="http://google.com" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-linkedin-square" aria-hidden="true" />
                        </a>

                        {/* Github */}
                        <a href="http://google.com" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-github-square" aria-hidden="true" />
                        </a>
                        {/* FCC */}
                        <a href="http://google.com" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-free-code-camp" aria-hidden="true" />
                        </a>

                        {/* Youtube */}
                        <a href="http://google.com" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-youtube-square" aria-hidden="true" />
                        </a>

                    </div>
                </div>

            </Grid>
        </div >
    }
}


export default LandlingPage;